
import Msg from 'vux/src/components/msg/index.vue'

import { fetchItem } from '@/api/mobile/children'
import avatar from '@/assets/image/header-avatar.png'
export default {
name: 'BindSuccess',
components: {
Msg
},
data() {
return {
description: 'msg description',
icon: '',
child_id: this.$route.query.child_id,
user: {},
doctor: '',
avatar
}
},
created() {
this.getData()
},
methods: {
getData() {
fetchItem({ child_id: this.child_id }).then(response => {
this.user = response.data
this.doctor = this.$store.state.Doctor.doctor === 'doctor' ? '医生' : '专属管理机构'
})
},
changeIcon() {
if (!this.icon || this.icon === 'success') {
this.icon = 'warn'
return
}
if (this.icon === 'warn') {
this.icon = 'info'
return
}
if (this.icon === 'info') {
this.icon = 'waiting'
return
}
if (this.icon === 'waiting') {
this.icon = 'success'
}
}
}
}



